<template>
  <article>
    <h1> Modal</h1>
    
    <h2></h2>

    <r-btn class="blue white-text" v-modal:modalA></r-btn>
    <r-modal id="modalA">
      <r-card>
        <r-card-text>
          <p class="txt">
            {{txt}}
          </p>
        </r-card-text>
        <r-card-row actions="actions">
          <r-placeholder/>
          <r-btn @click.native="modal('modalA')"></r-btn>
          <r-btn class="blue white-text" @click.native="modal('modalA')"></r-btn>
        </r-card-row>
      </r-card>
    </r-modal>

    <r-btn class="red white-text" v-modal:modalB></r-btn>
    <r-modal id="modalB" top>
      <r-card class="red white-text">
        <r-card-row actions="actions">
          <div class="modal-txt"></div>
          <r-btn class="blue white-text" large @click.native="modal('modalB')"></r-btn>
        </r-card-row>
      </r-card>
    </r-modal>

    <r-btn class="green white-text" v-modal:modalC></r-btn>
    <r-modal id="modalC" bottom>
      <r-card class="green white-text">
        <r-card-row actions="actions">
          <div class="modal-txt"></div>
          <r-btn class="blue white-text" @click.native="modal('modalC')"></r-btn>
        </r-card-row>
      </r-card>
    </r-modal>

    <h2>API</h2>
    
    <h3></h3>
    <p>
      v-modal modal
    </p>
    <table class="bordered responsive-table">
      <thead>
        <th></th>
        <th></th>
      </thead>
      <tbody>
        <tr>
          <td>v-modal:modalA</td>
          <td>
             arg ( modalA)  r-modal  id (modalA)
          </td>
        </tr>
      </tbody> 
    </table>

    <h3>props</h3>
    <table class="bordered responsive-table">
      <thead>
        <th></th>
        <th></th>
        <th></th>
        <th></th>
      </thead>
      <tbody>
        <tr>
          <td>id</td>
          <td>modal  id  v-modal  arg</td>
          <td>String</td>
          <td>-</td>
        </tr>
        <tr>
          <td>top</td>
          <td>modal </td>
          <td>Boolean</td>
          <td>false</td>
        </tr>
        <tr>
          <td>bottom</td>
          <td>modal </td>
          <td>Boolean</td>
          <td>false</td>
        </tr>
        <tr>
          <td>-</td>
          <td>modal  (top  bottom false)</td>
          <td>Boolean</td>
          <td>true</td>
        </tr>
        

      </tbody> 
    </table>


    <code>
      &lt;r-btn class="green white-text" v-modal:modal=""&gt;&lt;/r-btn&gt;
      &lt;r-modal id="modal" bottom&gt;
        &lt;r-card class="green white-text"&gt;
          &lt;r-card-row actions="actions"&gt;
            &lt;div&gt;&lt;/div&gt;
            &lt;r-placeholder/&gt;
            &lt;r-btn class="blue white--text" @click.native="modal('modal')"&gt;&lt;/r-btn&gt;
          &lt;/r-card-row&gt;
        &lt;/r-card&gt;
      &lt;/r-modal&gt;
    </code>
  </article>
</template>

<script>
export default {
  name: 'modals',
  data(){
    return {
      txt: 'I have to remind myself that some birds dont mean to be caged . Their feathers are just too bright. And when they fly away, the part of you that knows it was a sin to lock them up. DOES rejoice. Still, the place you live in is that much more drab and empty that theyre gone. I guess I just miss my friend. '
    }
  },
  methods:{
    modal (id) {
        this.$rubik.bridge.pub(`modal:close:${id}`)
      }
  }
}
</script>

<style scoped lang="stylus">
  .btn {
    margin 10px
  }
  .modal-txt {
    flex auto
    text-align center
    font-size 1.5rem
  }
</style>



