<template>
  <article>
    <h1> Select</h1>

    <h2></h2>

    <r-row>
      <r-col xs3>
        <r-select v-model="seled1" :options="options" id="test" label="Browser Select" dftDisabled name="test"  :dftText="dft"></r-select>
      </r-col>
    </r-row>
    <p v-if="seled1">
       {{seled1}}
    </p>

    <h3></h3>
    <r-row>
    <r-col xs3>
      <r-select v-model="seled2" :options="options" id="test" label="FF " name="test"  :dftText="dft"></r-select>
    </r-col>
    </r-row>

    <h3>Disabled Option</h3>
    <r-row>
    <r-col xs3>
      <r-select v-model="seled2" :options="options2" id="test" label="disabled option" name="test"  :dftText="dft"></r-select>
    </r-col>
    </r-row>
    
    <h3>props</h3>
    <table class="bordered responsive-table">
      <thead>
        <th></th>
        <th></th>
        <th></th>
        <th></th>
      </thead>
      <tbody>
        <tr>
          <td>label</td>
          <td>label </td>
          <td>String</td>
          <td>-</td>
        </tr>
        <tr>
          <td>id</td>
          <td>input[type=radio] id  label  for </td>
          <td>String</td>
          <td>-</td>
        </tr>
        <tr>
          <td>name</td>
          <td>input[type=radio] name </td>
          <td>String</td>
          <td>-</td>
        </tr>
        <tr>
          <td>options</td>
          <td>
            Option  <br>
            value: String, (optionvalue)<br>
            text: String, (optiontext)<br>
            disabled: Boolean (option)
          </td>
          <td>Array</td>
          <td>[]</td>
        </tr>
        <tr>
          <td>value</td>
          <td>input[type=radio]  value </td>
          <td>String</td>
          <td>-</td>
        </tr>
        <tr>
          <td>dftText</td>
          <td> option  text </td>
          <td>String</td>
          <td>-</td>
        </tr>
        <tr>
          <td>dftDisabled</td>
          <td> option </td>
          <td>String</td>
          <td>-</td>
        </tr>
        <tr>
          <td>disabled</td>
          <td>Select </td>
          <td>String</td>
          <td>-</td>
        </tr>
        <tr>
          <td>v-model </td>
          <td> select  value </td>
          <td>String</td>
          <td>-</td>
        </tr>
      </tbody> 
    </table>

    <code>
      &lt;r-select v-model="seled1" :options="options" id="test" label="Browser Select" dftDisabled name="test"  :dftText="dft"&gt;&lt;/r-select&gt;

      &lt;r-select v-model="seled2" :options="options" id="test" label="FF " name="test"  :dftText="dft"&gt;&lt;/r-select&gt;

      &lt;r-select v-model="seled2" :options="options2" id="test" label="disabled option" name="test"  :dftText="dft"&gt;&lt;/r-select&gt;
      
      export default {
        data(){
          return {
            dft: '',
            seled1: null,
            seled2: 'ff',
            options: [
              {
                value: 'chrome',
                text: 'Chrome',
              },
              {
                value: 'safari',
                text: 'Safari',
              },
              {
                value: 'ff',
                text: 'FireFox',
              }
            ],
            options2: [
              {
                value: 'chrome',
                text: 'Chrome',
              },
              {
                value: 'disabled',
                text: 'Disabled: ',
                disabled: true
              },
              {
                value: 'disabled',
                text: 'Disabled:  +1',
                disabled: true
              },
              {
                value: 'ff',
                text: 'FireFox',
              }
            ],
          }
        }
      }
    </code>
    
  </article>
</template>

<script>
export default {
  name: 'selects',
  data(){
    return {
      dft: '',
      seled1: null,
      seled2: 'ff',
      options: [
        {
          value: 'chrome',
          text: 'Chrome',
        },
        {
          value: 'safari',
          text: 'Safari',
        },
        {
          value: 'ff',
          text: 'FireFox',
        }
      ],
      options2: [
        {
          value: 'chrome',
          text: 'Chrome',
        },
        {
          value: 'disabled',
          text: 'Disabled: ',
          disabled: true
        },
        {
          value: 'disabled',
          text: 'Disabled:  +1',
          disabled: true
        },
        {
          value: 'ff',
          text: 'FireFox',
        }
      ],
    }
  }
}
</script>



