<template>
  <article>
    <h1> Checkbox</h1>

    <h2></h2>

    <h3>Basic</h3>
    <r-checkbox id="check0" :selected="sel" :val="'red'" label="Red"></r-checkbox>
    <r-checkbox id="check1" :selected="sel" :val="'yellow'" label="Yellow " checked="checked"></r-checkbox>
    <r-checkbox id="check2" :selected="sel" :val="'blue'" label="Blue"></r-checkbox>
    <p>
       {{ sel }}
    </p>

    <r-checkbox id="checkd" label="" checked="checked"></r-checkbox>
    <r-checkbox id="check-f" label="" filled></r-checkbox>
    <r-checkbox id="check-i" label=" Indeterminate Style" indeterminate></r-checkbox>
    
    <h3>Disabled</h3>
    <r-checkbox id="check-d1" label="Green" disabled="disabled"></r-checkbox>
    <r-checkbox id="check-d2" label="Brown" checked="checked" disabled="disabled"></r-checkbox>
    
    <h3>props</h3>
    <table class="bordered responsive-table">
      <thead>
        <th></th>
        <th></th>
        <th></th>
        <th></th>
      </thead>
      <tbody>
        <tr>
          <td>label</td>
          <td>label </td>
          <td>String</td>
          <td>''</td>
        </tr>
        <tr>
          <td>id</td>
          <td>input[type=checkbox] id  label  for </td>
          <td>String</td>
          <td>''</td>
        </tr>
        <tr>
          <td>name</td>
          <td>input[type=checkbox] name </td>
          <td>String</td>
          <td>''</td>
        </tr>
        <tr>
          <td>checked</td>
          <td>checkbox </td>
          <td>Boolean</td>
          <td>-</td>
        </tr>
        <tr>
          <td>disabled</td>
          <td>checkbox </td>
          <td>Boolean</td>
          <td>-</td>
        </tr>
        <tr>
          <td>val</td>
          <td>input[type=checkbox]  (valvalue)</td>
          <td>String</td>
          <td>-</td>
        </tr>
        <tr>
          <td>selected</td>
          <td> checkbox </td>
          <td>Array</td>
          <td>-</td>
        </tr>
        <tr>
          <td>filled</td>
          <td></td>
          <td>Boolean</td>
          <td>-</td>
        </tr>
        <tr>
          <td>indeterminate</td>
          <td></td>
          <td>Boolean</td>
          <td>-</td>
        </tr>
      </tbody> 
    </table>

    <code>
      &lt;r-checkbox id="check0" :selected="selected" :val="'red'" label="Red"&gt;&lt;/r-checkbox&gt;
      &lt;r-checkbox id="check1" :selected="selected" :val="'yellow'" label="Yellow " checked="checked"&gt;&lt;/r-checkbox&gt;
      &lt;r-checkbox id="check2" :selected="selected" :val="'blue'" label="Blue"&gt;&lt;/r-checkbox&gt;
      export default {
        name: 'checkboxes',
        data(){
          return {
            selected: ['red'],
          }
        }
      }

      &lt;r-checkbox id="checkd" label="" checked="checked"&gt;&lt;/r-checkbox&gt;
      &lt;r-checkbox id="check-f" label="" filled&gt;&lt;/r-checkbox&gt;
      &lt;r-checkbox id="check-i" label=" Indeterminate Style" indeterminate&gt;&lt;/r-checkbox&gt;
      
      &lt;r-checkbox id="check-d1" label="Green" disabled="disabled"&gt;&lt;/r-checkbox&gt;
      &lt;r-checkbox id="check-d2" label="Brown" checked="checked" disabled="disabled"&gt;&lt;/r-checkbox&gt;


    </code>

  </article>
</template>

<script>
export default {
  name: 'checkboxes',
  data(){
    return {
      sel: ['red'],
    }
  }
}
</script>



