<template>
  <article>
    <h1> Dropdown</h1>
    
    <h2></h2>

    <h3>Hover</h3>
    <span></span>
    <r-btn class="green white-text" v-dropdown:dropdownA><r-icon>arrow_drop_down</r-icon></r-btn>
    <r-dropdown id="dropdownA" :items="list" hover></r-dropdown>
    <br><br>
    <span></span>
    <r-btn class="green white-text" v-dropdown:dropdownB><r-icon>arrow_drop_down</r-icon></r-btn>
    <r-dropdown id="dropdownB" right hover>
      <li><a class="dropdown-item" href="javascript:;"></a></li>
      <li><a class="dropdown-item" href="javascript:;"></a></li>
      <li><a class="dropdown-item" href="javascript:;"></a></li>
      <li><a class="dropdown-item" href="javascript:;"><span class="red-after" v-badge:2></span></a></li>
    </r-dropdown>

    <h3>Click</h3>

    <r-btn class="red white-text" v-dropdown:dropdownC><r-icon>arrow_drop_down</r-icon></r-btn>
    <r-dropdown id="dropdownC" :items="list"></r-dropdown>

    <h3>Link / Text</h3>
    <a v-dropdown:dropdownD href="javascript:;">-</a>
    <r-dropdown id="dropdownD" :items="list"></r-dropdown>

    <span class="dropdown-span" v-dropdown:dropdownE href="javascript:;">-</span>
    <r-dropdown id="dropdownE" :items="list" right hover></r-dropdown>

    <h3>Icon</h3>
    <r-icon class="dropdown-icon" v-dropdown:dropdown>info</r-icon>
    <r-dropdown id="dropdown" :items="list"></r-dropdown>

    <h2>API</h2>
    <h3>: <strong>id</strong></h3>
    <p>
      v-dropdown  value  id  <br>
      API dropdownA dropdownB dropdownC
    </p>
    <h3>props</h3>
    <table class="bordered responsive-table">
      <thead>
        <th></th>
        <th></th>
        <th></th>
        <th></th>
      </thead>
      <tbody>
        <tr>
          <td>items</td>
          <td> { href:'', text:'' }</td>
          <td>Array</td>
          <td>[]</td>
        </tr>
        <tr>
          <td>right</td>
          <td></td>
          <td>Boolean</td>
          <td>false</td>
        </tr>
        <tr>
          <td>hover</td>
          <td>hover  click</td>
          <td>Boolean</td>
          <td>false</td>
        </tr>

      </tbody> 
    </table>

    <code>
      &lt;r-btn class="green white-text" v-dropdown:dropdownA&gt;&lt;r-icon&gt;arrow_drop_down&lt;/r-icon&gt;&lt;/r-btn&gt;
      &lt;r-dropdown id="dropdownA" :items="list" hover&gt;&lt;/r-dropdown&gt;

      // 
      &lt;r-btn class="green white-text" v-dropdown:dropdownB&gt;&lt;r-icon&gt;arrow_drop_down&lt;/r-icon&gt;&lt;/r-btn&gt;
      &lt;r-dropdown id="dropdownB" right hover&gt;
        &lt;li&gt;&lt;a class="dropdown-item" href=""&gt;&lt;/a&gt;&lt;/li&gt;
        &lt;li&gt;&lt;a class="dropdown-item" href=""&gt;&lt;/a&gt;&lt;/li&gt;
        &lt;li&gt;&lt;a class="dropdown-item" href=""&gt;&lt;/a&gt;&lt;/li&gt;
        &lt;li&gt;&lt;a class="dropdown-item" href=""&gt;&lt;span v-badge:2&gt;&lt;/span&gt;&lt;/a&gt;&lt;/li&gt;
      &lt;/r-dropdown&gt;

      &lt;r-btn class="red white-text" v-dropdown:dropdownC&gt;&lt;r-icon&gt;arrow_drop_down&lt;/r-icon&gt;&lt;/r-btn&gt;
      &lt;r-dropdown id="dropdownC" :items="list"&gt;&lt;/r-dropdown&gt;
    </code>
  </article>
</template>

<script>
export default {
  name: 'dropdowns',
  data(){
    return {
      list: [
        {
          href: 'javascript:;',
          text: ''
        },
        {
          href: 'javascript:;',
          text: ''
        },
        {
          href: 'javascript:;',
          text: ''
        },
        {
          href: 'javascript:;',
          text: ''
        }
      ]
    }
  }
}
</script>

<style scoped lang="stylus">
  .dropdown-span {
    display inline-block
    margin 0 20px
    cursor pointer
  }
  .dropdown-icon {
    display inline-block
    cursor pointer
    &:hover {
      color #f06
    }
  }
</style>



