<template>
  <article>
    <h1> Badge</h1>
    
    <h2></h2>
    
    <p>
      
    </p>

    <h3></h3>
    <r-icon class="badge-icons red-after" large v-badge:10.inner>call</r-icon>
    <r-icon class="badge-icons" large v-badge:star.inner.icon>face</r-icon>
