    <br>
    <span class="badge-link orange-after" v-badge:dot.left>This is text</span>
    
    <h2>API</h2>
    <h3></h3>
    <p>
       v-badge  class: '[color]-after' (color ) <br>
      PS:  after  [color]-after     
    </p>

    <h3></h3>
    <table class="bordered responsive-table">
      <thead>
        <th></th>
        <th></th>
      </thead>
      <tbody>
        <tr>
          <td>v-badge:star.inner.icon <br>  arg</td>
