<template>
  <article>
    <h1> File</h1>

    
    <h3></h3>
    <r-file v-model="file" placeholder=""></r-file>
    <div class="file-info" v-if="file.length">
      <p></p>
      <table class="centered responsive-table">
        <thead>
          <tr>
              <th>name</th>
              <th>lastModified</th>
              <th>lastModifiedDate</th>
              <th>size</th>
              <th>type</th>
          </tr>
        </thead>

        <tbody>
          <tr>
            <td>{{file[0].name}}</td>
            <td>{{file[0].lastModified}}</td>
            <td>{{file[0].lastModifiedDate}}</td>
            <td>{{file[0].size}}</td>
            <td>{{file[0].type}}</td>
          </tr>
        </tbody>
      </table>
    </div>

    <h3></h3>
    <r-file v-model="files" placeholder="" multiple></r-file>
    <div class="file-info" v-if="files.length">
      <p></p>
      <table class="centered responsive-table">
        <thead>
          <tr>
              <th>#</th>
              <th>name</th>
              <th>lastModified</th>
              <th>lastModifiedDate</th>
              <th>size</th>
              <th>type</th>
          </tr>
        </thead>

        <tbody>
          <tr v-for="(item, idx) in files.length">
            <td>{{item}}</td>
            <td>{{files[idx].name}}</td>
            <td>{{files[idx].lastModified}}</td>
            <td>{{files[idx].lastModifiedDate}}</td>
            <td>{{files[idx].size}}</td>
            <td>{{files[idx].type}}</td>
          </tr>
        </tbody>
      </table>
    </div>

    <h3>Disabled</h3>
    <r-file disabled placeholder=""></r-file>


    <h2>API</h2>

    <h3>props</h3>

    <table class="bordered responsive-table">
      <thead>
        <th></th>
        <th></th>
        <th></th>
        <th></th>
      </thead>
      <tbody>
        <tr>
          <td>name</td>
          <td>input[type=file] name </td>
          <td>String</td>
          <td>-</td>
        </tr>
        <tr>
          <td>disabled</td>
          <td>input[type=file] </td>
          <td>Boolean</td>
          <td>-</td>
        </tr>
        <tr>
          <td>multiple</td>
          <td></td>
          <td>Boolean</td>
          <td>-</td>
        </tr>
        <tr>
          <td>v-model </td>
          <td> input[type=file]  value </td>
          <td>String</td>
          <td>-</td>
        </tr>
      </tbody> 
    </table>


    <code>
      &lt;r-file v-model="file" placeholder=""&gt;&lt;/r-file&gt;
      &lt;r-file v-model="files" placeholder="" multiple&gt;&lt;/r-file&gt;
      &lt;r-file disabled placeholder=""&gt;&lt;/r-file&gt;
    </code>
    
  </article>
</template>

<script>
export default {
  name: 'files',
  data(){
    return {
      file: {},
      files: {},
    }
  }
}
</script>

<style lang="stylus" scoped>
  h3 {
    margin 2rem 0
  }
  .file-info {
    p {
      margin-top 2rem
      font-size 1.3rem
    }
  }
</style>



