<template>
  <article>
    <h1> Tooltip</h1>
    <p>
      tooltip  title 
    </p>
    <h2></h2>
    
    <h3></h3>
    <div class="tooltip-container">
      <div class="tt-top green white-text z-depth-1" v-tooltip:top="{ text: '' }"></div>
      <div class="tt-right green white-text z-depth-1" v-tooltip:right="{ text: '' }"></div>
      <div class="tt-bottom green white-text z-depth-1" v-tooltip:bottom="{ text: '' }"></div>
      <div class="tt-left green white-text z-depth-1" v-tooltip:left="{ text: '' }"></div>
    </div>

    <h3></h3>
    <r-btn class="grey white-text" v-tooltip:right="{ text: '' }"></r-btn>
    
    
    <h2>API</h2>

    <h3></h3>
    <table class="bordered responsive-table">
      <thead>
        <th></th>
        <th></th>
      </thead>
      <tbody>
        <tr>
          <td>v-tooltip:top="{ text: '' }"</td>
          <td> arg ( top)  tooltip </td>
        </tr>
        <tr>
          <td>v-tooltip:top="{ text: '' }"</td>
          <td> value  text  tooltip </td>
        </tr>
      </tbody> 
    </table>
    <code>
      &lt;span class="green white-text" v-tooltip:top="{ text: '' }"&gt;&lt;/span&gt;
      &lt;r-btn class="grey white-text" v-tooltip:right="{ text: '' }"&gt;&lt;/r-btn&gt;
    </code>
  </article>
</template>

<script>
export default {
  name: 'tooltips'
}
</script>

<style scoped lang="stylus">
  .tooltip-container {
    position relative
    width 140px
    height 100px
    margin 4rem 2rem
    div {
      position absolute
      width 4.5rem
      height 1.8rem
      line-height 1.8rem
      font-size 1.3rem
      text-align center
      border-radius 5px
      cursor pointer
    }
  }
  .tt-top {
    top 0
    left 50%
    transform translateX(-50%)
  }
  .tt-right {
    right 0
    top 50%
    transform translateY(-50%)
  }
  .tt-bottom {
    bottom 0
    left 50%
    transform translateX(-50%)
  }
  .tt-left {
    left 0
    top 50%
    transform translateY(-50%)
  }
</style>



