<template>
  <nav>
    <ul>
      <li>
        <router-link :class="active == 'about' || 'home' && activeClass" :to="{path: '/about'}">
          <r-icon>face</r-icon>
          <span></span>
        </router-link>
      </li>
      <li>
        <div>CSS</div>
        <ul>
          <li v-for="item in css">
            <router-link :class="active == item.name && activeClass" :to="{path: '/'+item.name}">
              <r-icon>{{item.icon}}</r-icon>
              <span>{{item.txt}}</span>
            </router-link>
          </li>
        </ul>
      </li>
      <li>
        <div></div>
        <ul>
          <li v-for="item in components">
            <router-link :class="active == item.name && activeClass" :to="{path: '/'+item.name}">
              <r-icon>{{item.icon}}</r-icon>
              <span>{{item.txt}}</span>
            </router-link>
          </li>
        </ul>
      </li>
      <li>
        <div></div>
        <ul>
          <li v-for="item in forms">
            <router-link :class="active == item.name && activeClass" :to="{path: '/'+item.name}">
              <r-icon>{{item.icon}}</r-icon>
              <span>{{item.txt}}</span>
            </router-link>
          </li>
        </ul>
      </li>
      <li>
        <div></div>
        <ul>
          <li v-for="item in directives">
            <router-link :class="active == item.name && activeClass" :to="{path: '/'+item.name}">
              <r-icon>{{item.icon}}</r-icon>
              <span>{{item.txt}}</span>
            </router-link>
          </li>
        </ul>
      </li>

    </ul>
    
  </nav>
</template>

<script>
export default {
  name: 'navigator',
  data() {
    return {
      activeClass: 'active',
      css: [
        {
          name: 'grid',
          icon: 'grid_on',
          txt: ' Grid'
        },{
          name: 'color',
          icon: 'palette',
          txt: ' Color'
        },{
          name: 'shadow',
          icon: 'movie',
          txt: ' Shadow'
        },{
          name: 'table',
          icon: 'border_all',
          txt: ' Table'
        }
      ],
      components: [
        {
          name: 'alert',
          icon: 'add_alert',
          txt: ' Alert'
        },{
          name: 'breadcrumb',
          icon: 'arrow_forward',
          txt: ' Breadcrumb'
        },{
          name: 'card',
          icon: 'book',
          txt: ' Card'
        },{
          name: 'tag',
          icon: 'beenhere',
          txt: ' Tag'
        },{
          name: 'icon',
          icon: 'toys',
          txt: ' Icon'
        },{
          name: 'button',
          icon: 'credit_card',
          txt: ' Button'
        },{
          name: 'dropdown',
          icon: 'arrow_drop_down_circle',
          txt: ' Dropdown'
        },{
          name: 'tab',
          icon: 'tab',
          txt: ' Tabs'
        },{
          name: 'slider',
          icon: 'play_circle_outline',
          txt: ' Slider'
        },{
          name: 'modal',
          icon: 'chat',
          txt: ' Modal'
        },{
          name: 'pagination',
          icon: 'last_page',
          txt: ' Pagination'
        },{
          name: 'toast',
          icon: 'crop_landscape',
          txt: ' Toast'
        }
      ],
      forms: [
        {
          name: 'input',
          icon: 'input',
          txt: ' Input'
        },{
          name: 'file',
          icon: 'file_upload',
          txt: ' File'
        },{
          name: 'radio',
          icon: 'radio_button_checked',
          txt: ' Radio'
        },{
          name: 'checkbox',
          icon: 'check_box',
          txt: ' Checkbox'
        },{
          name: 'select',
          icon: 'keyboard_arrow_down',
          txt: ' Select'
        },
      ],
      directives: [
        {
          name: 'waves',
          icon: 'leak_add',
          txt: ' Waves'
        },
        {
          name: 'badge',
          icon: 'fiber_manual_record',
          txt: ' Badge'
        },{
          name: 'tooltip',
          icon: 'swap_vert',
          txt: ' Tooltip'
        },{
          name: 'img-lazy',
          icon: 'image',
          txt: ' Lazy'
        }
      ]
    }
  },
  computed: {
    active() {
      return this.$route.name
    }
  }
}
</script>

<style lang="stylus">
@import '../style/variable.styl'
nav {
  font-size 14px
  padding 15px
  a {
    display flex
    position relative
    padding 10px 0 10px 10px
    color #525558
    overflow hidden
    text-overflow ellipsis
    white-space nowrap
    &.v-link-active,
    &:hover,
    &.active {
      color $txt-green
    }
    .material-icons {
      font-size 20px
      margin-right 5px
    }
    span {
      line-height 24px
    }
  }
  div {
    font-size 16px
    margin 8px 0
  }
}
@media (max-width: 700px) {
  nav {
    padding 0
  }
  nav a {
    font-size 12px
  }
}


</style>



