<template>
  <article>
    <h1> Toast</h1>

    <p>
       toast  
    </p>

    <h2></h2>
    
    <r-btn info @click.native="toast(top)"></r-btn>
    <r-btn info @click.native="toast(right)"></r-btn>
    <r-btn info @click.native="toast(bottom)"></r-btn>
    <r-btn info @click.native="toast(left)"></r-btn>

    <h3></h3>
    <p> 3 </p>
    <r-btn info @click.native="toast(bottom2)">0.5</r-btn>
    <r-btn info @click.native="toast(bottom10)">10</r-btn>

    <h3></h3>
    <r-btn success @click.native="toast(callback)"></r-btn>

    <h2>API</h2>
    <p>
       toast  Vue  toast.fire() 
    </p>

    <h3></h3>
    <p>
      fire  <br> 
      fire (msg, position, countdown, fn)
    </p>
    <table class="bordered responsive-table">
      <thead>
        <th></th>
        <th></th>
        <th></th>
        <th></th>
      </thead>
      <tbody>
        <tr>
          <td>msg</td>
          <td></td>
          <td>String</td>
          <td>-</td>
        </tr>
        <tr>
          <td>position</td>
          <td> : top right bottom left</td>
          <td>String</td>
          <td>bottom</td>
        </tr>
        <tr>
          <td>coundown</td>
          <td> : ms ()</td>
          <td>Number</td>
          <td>3000</td>
        </tr>
        <tr>
          <td>fn</td>
          <td></td>
          <td>Function</td>
          <td>-</td>
        </tr>

      </tbody> 
    </table>

    <code>
      const fn = () => alert('toast ')
      export default {
        name: 'toast',
        data(){
          return {
            top: ['', 'top'],
            right: ['', 'right'],
            bottom: ['', 'bottom'],
            left: ['', 'left'],
            bottom2: ['', 'bottom', 500],
            bottom10: ['', 'bottom', 10000],
            callback: ['2', 'bottom', 2000, fn],
          }
        },
        methods: {
          toast (data) {
            // toast  fire
            this.$rubik.toast.fire(...data)
          }
        }
      }
    </code>
  </article>
</template>

<script>
const fn = () => alert('toast ')
export default {
  name: 'toast',
  data(){
    return {
      top: ['', 'top'],
      right: ['', 'right'],
      bottom: ['', 'bottom'],
      left: ['', 'left'],
      bottom2: ['', 'bottom', 500],
      bottom10: ['', 'bottom', 10000],
      callback: ['2', 'bottom', 2000, fn],
    }
  },
  methods: {
    toast (data) {
      this.$rubik.toast.fire(...data)
    }
  }
}
</script>



