<template>
  <article>
    <h1> Radio</h1>

    <h2></h2>
      
    <h3>Basic</h3>
    <r-radio id="gender-male" name="gender" v-model="gender" value="" label=""></r-radio>
    <r-radio id="gender-female" name="gender" v-model="gender" value="" label=""></r-radio>

    <p>{{ gender }}</p>

    <r-radio id="checked-yes" name="checked" label="" checked></r-radio>

    <h3></h3>
    <r-radio id="color-b" name="color" dot value="" v-model="color" label=""></r-radio>
    <r-radio id="color-w" name="color" dot value="" v-model="color" label=""></r-radio>

    <p>{{ color }}</p>

    <h3>disabled</h3>
    <r-radio id="d1" name="d1" label="disabled  " dot checked ></r-radio>
    <r-radio id="d2" name="d2" label="disabled " disabled checked></r-radio>
    <r-radio id="d3" name="d3" label="disabled " disabled></r-radio>

    <h2>API</h2>

    <h3>props</h3>
    <table class="bordered responsive-table">
      <thead>
        <th></th>
        <th></th>
        <th></th>
        <th></th>
      </thead>
      <tbody>
        <tr>
          <td>label</td>
          <td>label </td>
          <td>String</td>
          <td>-</td>
        </tr>
        <tr>
          <td>id</td>
          <td>input[type=radio] id  label  for </td>
          <td>String</td>
          <td>-</td>
        </tr>
        <tr>
          <td>name</td>
          <td>input[type=radio] name </td>
          <td>String</td>
          <td>-</td>
        </tr>
        <tr>
          <td>checked</td>
          <td>radio </td>
          <td>String</td>
          <td>-</td>
        </tr>
        <tr>
          <td>disabled</td>
          <td>radio </td>
          <td>Boolean</td>
          <td>-</td>
        </tr>
        <tr>
          <td>value</td>
          <td>input[type=radio]  value </td>
          <td>String</td>
          <td>-</td>
        </tr>
        <tr>
          <td>v-model </td>
          <td> input[type=radio]  value </td>
          <td>String</td>
          <td>-</td>
        </tr>
      </tbody> 
    </table>

    <code>
      &lt;r-radio id="gender-male" name="gender" v-model="gender" value="" label=""&gt;&lt;/r-radio&gt;
      &lt;r-radio id="gender-female" name="gender" v-model="gender" value="" label=""&gt;&lt;/r-radio&gt;
      &lt;r-radio id="checked-yes" name="checked" label="" checked&gt;&lt;/r-radio&gt;

      &lt;r-radio id="color-b" name="color" dot label=""&gt;&lt;/r-radio&gt;
      &lt;r-radio id="color-w" name="color" dot label=""&gt;&lt;/r-radio&gt;

      &lt;r-radio id="d1" name="d1" label="disabled  " dot checked &gt;&lt;/r-radio&gt;
      &lt;r-radio id="d2" name="d2" label="disabled " disabled checked&gt;&lt;/r-radio&gt;
      &lt;r-radio id="d3" name="d3" label="disabled " disabled&gt;&lt;/r-radio&gt;
      
      &lt;script&gt;
        export default {
          data(){
            return {
              gender: '',
              color: '',
            }
          }
        }
      &lt;/script&gt;

    </code>

  </article>
</template>

<script>
export default {
  name: 'radios',
  data(){
    return {
      gender: '',
      color: '',
    }
  }
}
</script>

<style lang="stylus" scoped>
  h3 {
    margin 2rem 0
  }
</style>



