<template>
  <article>
    <h1> Waves</h1>
    
    <h2></h2>
    <h3></h3>
    <r-btn class="white black-text z-depth-2" large v-wave="{color:'purple'}"> Waves</r-btn>
    <r-btn class="white black-text z-depth-2" large v-wave="{color:'red'}"> Waves</r-btn>
    <br><br>
    <r-btn class="blue white-text" large v-wave.light> Waves</r-btn>
    <r-btn class="blue white-text" large v-wave="{color:'white'}"> Waves</r-btn>
    
    <h3></h3>
    
    <div class="wave-demo z-depth-1 grey white-text" v-wave="{color:'green'}">div  Waves</div>
    <br><br>
    <p class="wave-demo z-depth-1 red lighten-1 white-text" v-wave="{color:'white'}">p  Waves</p>
    
    <h2>API</h2>
    
    <h3></h3>

    <table class="bordered responsive-table">
      <thead>
        <th></th>
        <th></th>
      </thead>
      <tbody>
        <tr>
          <td>v-wave="{color:'purple'}"</td>
          <td> value  color </td>
        </tr>
        <tr>
          <td>v-wave.light</td>
          <td>  value  color </td>
        </tr>
      </tbody> 
    </table>

    <p>
       position: relative !important 
    </p>
    <code>
      &lt;r-btn class="white black-text" large v-wave="{color:'purple'}"&gt; Waves&lt;/r-btn&gt;
      &lt;r-btn class="blue white-text" large v-wave.light&gt; Waves&lt;/r-btn&gt;
      &lt;div class="grey white-text" v-wave="{color:'green'}"&gt;div  Waves&lt;/div&gt;
    </code>
  </article>
</template>

<script>
export default {
  name: 'waves'
}
</script>

<style scoped lang="stylus">
  button {
    margin 0 1rem 1rem 0
  }
  .wave-demo {
    display inline-block
    padding 0 3rem
    min-height 3rem
    line-height 3rem
    font-size 1.1rem
    border-radius 3px
    cursor pointer
  }
</style>



