<template>
  <article>
    <h1> Alert</h1>

    <h2></h2>

    <div class="alert-container">
      <span> INFO </span>
      <r-alert info>{{ txt }} </r-alert>

      <span> SUCCESS </span>
      <r-alert success >{{ txt }}</r-alert>
      
      <span> WARNING </span>
      <r-alert warning >{{ txt }}</r-alert>
      
      <span> ERROR </span>
      <r-alert error >{{ txt }}</r-alert>
      
    </div>

    <h3> icon</h3>
    <div class="alert-container">
      <r-alert warning no-icon ></r-alert>
      <r-alert info no-icon ></r-alert>
    </div>

    <h3> icon</h3>
    <div class="alert-container">
      <r-alert warning icon="local_taxi" ></r-alert>
      <r-alert info icon="bluetooth" ></r-alert>
    </div>

    <h3> icon</h3>
    <div class="alert-container">
      <r-alert warning icon="local_taxi" close></r-alert>
      <r-alert info icon="bluetooth" close ></r-alert>
    </div>

    <h2>API</h2>
    
    <h3>props</h3>
    <table class="bordered responsive-table">
      <thead>
        <th></th>
        <th></th>
      </thead>
      <tbody>
        <tr>
          <td>info</td>
          <td> </td>
        </tr>
        <tr>
          <td>success</td>
          <td> </td>
        </tr>
        <tr>
          <td>warning</td>
          <td> </td>
        </tr>
        <tr>
          <td>error</td>
          <td> </td>
        </tr>
        <tr>
          <td>no-icon</td>
          <td> icon</td>
        </tr>
        <tr>
          <td>close</td>
          <td> icon</td>
        </tr>

        <tr>
          <td>icon</td>
          <td> icon</td>
        </tr>

      </tbody> 
    </table>

    <code>
      &lt;r-alert info&gt;...&lt;/r-alert&gt;
      &lt;r-alert warning no-icon &gt;...&lt;/r-alert&gt;
      &lt;r-alert warning icon="local_taxi" &gt;...&lt;/r-alert&gt;
      &lt;r-alert warning icon="local_taxi" close&gt;...&lt;/r-alert&gt;
    </code>
  </article>
</template>

<script>
export default {
  name: 'alerts',
  data(){
    return {
      txt: ''
    }
  }
}
</script>

<style scoped lang="stylus">
  .alert-container {
    width 70%
    .alert {
      margin 10px 0
    }
  }
  @media (max-width: 600px) {
    .alert-container {
      width 100%
    }
  }
</style>




