<template>
  <article>
    <h1> Tag</h1>

    <h2></h2>

    <div class="tag-container">
      <r-tag></r-tag>
      <r-tag class="blue white-text"></r-tag>
      <r-tag class="red white-text"></r-tag>
      <r-tag small class="green white-text"></r-tag>
    </div>
    
    <h3> icon</h3>
    <div class="tag-container">
      <r-tag>
        <r-icon left>face</r-icon>
      </r-tag>

      <r-tag>
        <r-icon left>directions_bike</r-icon>
      </r-tag>

      <r-tag class="pink white-text">
        <r-icon left>local_gas_station</r-icon>
      </r-tag>

      <r-tag class="black white-text">
        <r-icon>flight</r-icon>
      </r-tag>  
    </div>

    <h3></h3>
    <div class="tag-container">
      <r-tag outline class="blue gray-text"></r-tag>
      <r-tag outline class="red gray-text"></r-tag>
    </div>

    <h3></h3>
    <div class="tag-container">
      <r-tag rect class="blue white-text"></r-tag>
      <r-tag rect>
        <r-icon right>local_gas_station</r-icon>
      </r-tag>
    </div>

    <h3></h3>
    <div class="tag-container">
      <r-tag class="blue white-text" close></r-tag>
      <r-tag class="pink black-text" close></r-tag>
    </div>

    <h2>API</h2>
    <h3>props</h3>
    <table class="bordered responsive-table">
      <thead>
        <th></th>
        <th></th>
        <th></th>
        <th></th>
      </thead>
      <tbody>
        <tr>
          <td>close</td>
          <td></td>
          <td>Boolean</td>
          <td>-</td>
        </tr>
        <tr>
          <td>rect</td>
          <td></td>
          <td>Boolean</td>
          <td>-</td>
        </tr>
        <tr>
          <td>outline</td>
          <td></td>
          <td>Boolean</td>
          <td>-</td>
        </tr>
        <tr>
          <td>small</td>
          <td></td>
          <td>Boolean</td>
          <td>-</td>
        </tr>

      </tbody> 
    </table>
    <code>
      &lt;r-tag&gt;&lt;/r-tag&gt;

       &lt;r-tag small class="green white-text"&gt; &lt;/r-tag&gt;

      // 
      &lt;r-tag class="blue white-text"&gt;&lt;/r-tag&gt;

      // icon
      &lt;r-tag&gt;
        &lt;r-icon left&gt;face&lt;/r-icon&gt;
      &lt;/r-tag&gt;

      // 
      &lt;r-tag outline class="blue gray-text"&gt;&lt;/r-tag&gt;

      // 
      &lt;r-tag rect class="blue white-text"&gt;&lt;/r-tag&gt;

      // 
      &lt;r-tag class="blue white-text" close&gt;&lt;/r-tag&gt;
    </code>
  </article>
</template>

<script>
export default {
  name: 'tags',
}
</script>

<style scoped lang="stylus">
  .tag-container {
    margin 1rem 0
    .tag {
      margin .5rem 
      a {
        color #fff
      }
    }
  }
</style>



